@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 62.5%;
  background-image: linear-gradient( 94.3deg,  rgba(26,33,64,1) 10.9%, rgba(81,84,115,1) 87.1% );
  height: 100vh;
}
.banner{
  display: grid;
  position:absolute;
  width: 100vw;
  min-height: 50vh;
  min-height: 50vh;
  -webkit-columns: 1fr 1fr;
          columns: 1fr 1fr;
  border-color: red;
  background-color: white;
  z-index: 0;
  -webkit-clip-path: polygon(100% 0, 100% 21%, 100% 60%, 100% 100%, 0 100%, 0 100%, 36% 0);
          clip-path: polygon(100% 0, 100% 21%, 100% 60%, 100% 100%, 0 100%, 0 100%, 36% 0);
  background-image: url(https://i.postimg.cc/xd1bNz4R/banner.png);
  background-repeat: round;
  z-index: -1;
}
.my-name{
  position:absolute;
  color: rgb(247, 169, 0);
  font-size: 7rem;
  padding-left: 6rem;
   z-index: 1;
  font-family: 'IBM Plex Serif', serif;
}
.separador{
  width: 100%;
  height: 50vh;
}
.separador_img{
  z-index: 1;
  float: right;
  border-radius:50%;
  margin: 1rem;
  width: 9vw;
}
.Main-header{
    display: flex;
    background-color: rgba(0, 0, 0, 0.261);
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    min-height: 10vh;
   position:-webkit-sticky;
   position:sticky;
}
.navbar{
    margin: 2rem;
}
.navbar__li , a{
    font-size: 2.5rem;
    list-style: none;
    color: white;
}
a{
    text-decoration: none;
    border-bottom: 1px solid rgb(247, 169, 0);
}
.recuadro img{
    min-width: 25rem;
    min-height: 25rem;
    max-width: 25rem;
    max-height: 25rem;
}
.container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    max-width: 70vw;
    text-align: center;
    justify-content: center;
    margin: 0 auto;
}
.recuadro{
    margin: 2rem;
    color: white;
    font-size: 2rem;
}
.range{
    height: 20px;
    width: 100%;
    background-color: #f1f1f1;
    border-radius: 50px;
    -webkit-appearance: none;
    }
.range:disabled{
    color: red;
}
